:root {
  --left-bg-color: #effbf7;
  --right-bg-color: #3dbfc1;
  --left-color: #197477;
  --right-color: #202124;
  --pr-bg-color: #96c75c;
  --pr-color: #effbf7;
}

.about {
  position: absolute;
  height: 100vh;
  width: 100vw;
  display: flex;
  overflow: hidden;
}

.skills {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-color: #3dbfc1;
  background-color: var(--right-bg-color);
  transition: width 2s linear;
}

.skillWrap {
  display: grid;
  grid-template-columns: 30% 70%;
  justify-content: center;
  align-items: center;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: 100%;
}

.skillWrap > h3 {
  display: inline block;
  margin-right: 25px;
  font-size: calc(1vw + 1vh + 1vmin);
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.wrapper {
  height: 100%;
  width: 80%;
  color: #202124;
  color: var(--right-color);
  margin: 15vh 0;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.progress-bar {
  height: calc(1vmax + 1.5vmin);
  width: 100%;
  background-color: #effbf7;
  background-color: var(--pr-color);
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 2px 0 10px inset rgba(0, 0, 0, 0.2);
  position: relative;
}

.bar {
  width: 0;
  height: 100%;
  background-color: #96c75c;
  background-color: var(--pr-bg-color);

  background-image: linear-gradient(
    -45deg,
    rgba(255, 255, 255, 0.2) 25%,
    transparent 25%,
    transparent 50%,
    rgba(255, 255, 255, 0.2) 50%,
    rgba(255, 255, 255, 0.2) 75%,
    transparent 75%,
    transparent
  );

  background-size: 30px 30px;
  -webkit-animation: move 2s linear infinite;
          animation: move 2s linear infinite;
  box-shadow: 2px 0 10px inset rgba(0, 0, 0, 0.2);
  transition: width 2.5s ease-out;
}

@-webkit-keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 30px 30px;
  }
}

@keyframes move {
  0% {
    background-position: 0 0;
  }
  100% {
    background-position: 30px 30px;
  }
}

.perc {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: #202124;
  font-weight: bold;
}

.meInfo {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  box-sizing: border-box;
  background-color: #effbf7;
  background-color: var(--left-bg-color);
  transition: width 2s linear;
}

.aboutSectionContainer {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 85%;
  height: 80%;
  margin: auto;
}

.summary {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(calc(11vw + 11vh), 1fr));
  grid-column-gap: calc(4vw + 4vh + 4vmin);
  justify-content: center;
  align-items: center;
  width: 100%;
  color: #197477;
  color: var(--left-color);
  text-align: left;
}

.octaWrap {
  width: calc(13vw + 13vh + 6.5vmin);
  height: calc(13vw + 13vh + 6.5vmin);
  float: left;
  position: relative;
  overflow: hidden;
  margin-right: calc(3vw + 3vh + 0.8vmin);
}

.profileImage {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  overflow: hidden;
  transform: rotate(45deg);
  border: 8px double #3dbfc1;
  border-collapse: collapse;
}

.profileImage:after {
  background: url(/portfolio-v1/static/media/image.08774d29.jpg) no-repeat center/contain;
  position: absolute;
  top: -8px;
  right: -8px;
  bottom: -8px;
  left: -8px;
  transform: rotate(-45deg);
  content: "";
  border: inherit;
}

.aboutName {
  justify-content: center;
  font-family: "Spectral SC", serif;
  font-size: calc(1.6vw + 1.6vh + 0.8vmin);
  line-height: calc(4vw + 4vh + 2vmin);
  letter-spacing: 2px;
  word-spacing: 5px;
}

.info {
  display: inline-block;
  line-height: calc(1.8vw + 1.8vh + 0.9vmin);
  font-size: 1.8vw;
  color: #197477;
  color: var(--left-color);
  width: 100%;
  margin: 0 auto;
  text-align: left;
  list-style: none;
}

/* .info {
  width: 85%;
  margin: 0 auto;
  height: 30%;
  background: url("./me-info.PNG") no-repeat center/contain;
} */

.ContactMe {
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 40vh;
  left: calc(50vw - (4vw + 4vh + 2vmin));
  transform: translate(-50%, -50%);
  text-decoration: none !important;
  cursor: pointer;
  color: #262626;
  width: calc(8vw + 8vh + 4vmin);
  height: calc(2.6vw + 2.6vh + 1.3vmin);
  font-family: verdana;
  font-size: calc(1vw + 1vh + 0.5vmin);
  text-transform: uppercase;
  z-index: 3;
  background-color: #effbf7;
  background-color: var(--left-bg-color);
  transform: rotate(90deg);
}

.ContactMe:before {
  content: "";
  width: calc(8vw + 8vh + 4vmin);
  height: calc(2.6vw + 2.6vh + 1.3vmin + 3vmin);
  position: absolute;
  left: 0;
  top: -1.5vmin;
  border-left: 2px solid #262626;
  border-right: 2px solid #262626;
  transition: 1s;
}

.ContactMe:after {
  content: "";
  width: calc(8vw + 8vh + 4vmin + 3vmin);
  height: calc(2.6vw + 2.6vh + 1.3vmin);
  position: absolute;
  left: -1.5vmin;
  top: 0;
  border-top: 2px solid #262626;
  border-bottom: 2px solid #262626;
  transition: 1s;
}

.ContactMe:hover:before {
  transform: rotateY(180deg);
}

.ContactMe:hover:after {
  transform: rotateX(181deg);
}

@media (max-width: 1200px) {
  li {
    font-size: 3vh;
    line-height: calc(2.2vw + 2.2vh + 1.1vmin);
    font-weight: 700;
  }  
  body,
  html {
    height: 160vh;
  }
}

@media (max-width: 960px) {

  .about {
    height: 160vh;
    flex-direction: column;
    overflow-y: auto;
  }

  .aboutSectionContainer {
    height: 75%;
  }

  .summary {
    display: flex;
    flex-direction: column;
    align-items: end;
  }

  .aboutName {
    position: absolute;
    left: 45vw;
    transform: rotate(90deg);
    font-size: calc(2vw + 2vh + 1.5vmin);
    font-weight: 900;
  }

  .ContactMe {
    top: calc(80vh - (2.5vw + 2.5vh + 1.25vmin));
    left: calc(50vw - (6vw + 6vh + 3vmin));
    width: calc(12vw + 12vh + 6vmin);
    height: calc(5vw + 5vh + 2.5vmin);
    transform: rotate(0deg);
  }

  .ContactMe:before {
    width: calc(12vw + 12vh + 6vmin);
    height: calc(5vw + 5vh + 2.5vmin + 8vmin);
    left: 0;
    top: -4vmin;
  }

  .ContactMe:after {
    width: calc(12vw + 12vh + 6vmin + 8vmin);
    height: calc(5vw + 5vh + 2.5vmin);
    left: -4vmin;
    top: 0;
  }

  .progress-bar {
    height: calc(1.5vmax + 2vmin);
  }

  .aboutSectionContainer {
    margin-top: 5%;
  }
}

:root {
  --primary-color: #010712;
  --secondary-color: #818386;
  --bg-color: #fcfdfd;
  --button-color: #3b3636;
  --h1-color: #3f444c;
}

[data-theme="dark"] {
  --primary-color: #fcfdfd;
  --secondary-color: #818386;
  --bg-color: #010712;
  --button-color: #818386;
  --h1-color: #fcfdfd;
}

.contact > * {
  margin: 0;
  box-sizing: border-box;
  transition: all 0.3s ease-in-out;
}

.contact {
  display: flex;
  width: 100vw;
  height: 100vh;
  background: #fcfdfd;
  background: var(--bg-color);
  overflow: hidden;
}

.left-col {
  width: 30vw;
  height: 100%;
  background: url(/portfolio-v1/static/media/react.34aa98c4.jpg) no-repeat center/cover;
}

.right-col {
  background: #fcfdfd;
  background: var(--bg-color);
  width: 70vw;
  height: 100vh;
  padding: 4vh 3vw;
}

h1,
label,
button,
.description {
  font-family: "Jost", sans-serif;
  font-weight: 400;
  letter-spacing: 0.1rem;
}

h1 {
  color: #3f444c;
  color: var(--h1-color);
  text-transform: uppercase;
  font-size: calc(1.6vw + 1.6vh + 0.8vmin);
  letter-spacing: 0.5rem;
  font-weight: 300;
}

p {
  color: #818386;
  color: var(--secondary-color);
  font-size: calc(1vw + 1vh + 0.5vmin);
  letter-spacing: 0.01rem;
  width: 40vw;
  margin: 0.25rem 0;
}

label,
.description {
  color: #818386;
  color: var(--secondary-color);
  text-transform: uppercase;
  font-size: calc(0.6vw + 0.6vh + 0.3vmin);
}

form {
  width: 100%;
  position: relative;
  margin-top: 2rem;
  padding: 1rem 0;
  margin: 5vh auto;
}

input,
textarea,
label {
  width: 80%;
  display: block;
}

p,
placeholder,
input,
textarea {
  font-family: "Helvetica Neue", sans-serif;
}

input::-webkit-input-placeholder, textarea::-webkit-input-placeholder {
  color: #010712;
  color: var(--primary-color);
}

input:-ms-input-placeholder, textarea:-ms-input-placeholder {
  color: #010712;
  color: var(--primary-color);
}

input::-ms-input-placeholder, textarea::-ms-input-placeholder {
  color: #010712;
  color: var(--primary-color);
}

input::placeholder,
textarea::placeholder {
  color: #010712;
  color: var(--primary-color);
}

input,
textarea {
  color: #010712;
  color: var(--primary-color);
  font-weight: 500;
  background: #fcfdfd;
  background: var(--bg-color);
  border: none;
  border-bottom: 1px solid #818386;
  border-bottom: 1px solid var(--secondary-color);
  padding: 0.5rem 0;
  margin-bottom: 1rem;
  outline: none;
}

textarea {
  resize: none;
}

button {
  text-transform: uppercase;
  font-weight: 300;
  background: #3b3636;
  background: var(--button-color);
  color: #fcfdfd;
  color: var(--bg-color);
  width: calc(6vw + 6vh + 3vmin);
  height: 2.25rem;
  border: none;
  border-radius: 2px;
  outline: none;
  cursor: pointer;
}

input:hover,
textarea:hover,
button:hover {
  opacity: 0.5;
}

button:active {
  opacity: 0.8;
}

/* Toggle Switch */

.theme-switch-wrapper {
  display: flex;
  align-items: center;
  text-align: center;
  height: 5vmax;
  width: 18.5vmax;
  position: absolute;
  top: 4vh;
  right: 2vw;
}

.description {
  margin-left: 1.25rem;
  font-size: 2vmax;
  margin-left: 0.4rem;
}

.theme-switch {
  display: inline-block;
  position: relative;
  height: 3.8vmax;
  width: 12vmax;
}

.theme-switch input {
  display: none;
}

.slider {
  background-color: #ccc;
  bottom: 0;
  cursor: pointer;
  left: 0;
  position: absolute;
  right: 0;
  top: 0;
  transition: 0.4s;
}

.slider:before {
  background-color: #fff;
  bottom: 0.4vmax;
  content: "";
  width: 3vmax;
  height: 3vmax;
  left: 0.4vmax;
  position: absolute;
  transition: 0.4s;
}

input:checked + .slider {
  background-color: #3b3636;
  background-color: var(--button-color);
}

input:checked + .slider:before {
  transform: translateX(5vw);
}

.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

#error,
#success-msg {
  width: 40vw;
  margin: 0.125rem 0;
  font-size: 0.75rem;
  text-transform: uppercase;
  font-family: "Jost";
  color: #818386;
  color: var(--secondary-color);
}

#success-msg {
  transition-delay: 3s;
}

@media only screen and (max-width: 1100px) {
  body,
  html {
    height: 100vh;
  }

  .logo {
    width: 8rem;
  }
  h1 {
    font-size: 5vh;
    margin-top: 5vh;
  }
  p {
    font-size: 2vh;
  }

  .right-col {
    height: 100%;
    width: 80vw;
    margin: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    text-align: left;
  }

  .left-col {
    width: 20vw;
  }

  form {
    /* display: flex;
    flex-direction: column;
    justify-content: space-around;
    height: 80%; */
  }

  input,
  textarea,
  button {
    font-size: 2vmax;
  }
  
  button {
    width: 16vh;
    height: 6vh;
  }

  .theme-switch input {
    display: none;
  }

  input:checked + .slider:before {
    transform: translateX(5vmax);
  }

  .slider.round {
    border-radius: 2vmax;
  }

  .slider.round:before {
    border-radius: 50%;
  }
}

@media (max-width: 600px) {
  .left-col {
    display: none;
  }
  .right-col {
    width: 100vw;
    height: 80%;
    align-items: center;
  }

  form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-align: center;
  }

  p {
    width: 80%;
    -webkit-margin-before: 2vh;
            margin-block-start: 2vh;
    -webkit-margin-start: 1vw;
            margin-inline-start: 1vw;
  }
}

